.MusicPlayer_Component {
    height: 92vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.MusicPlayer_Component > div {
    border: 1px solid black;
    height: 450px;
    width: 500px;
    border-radius: 5px;
    display: grid;
    grid-template-columns: 35% 65%;
}

.playlist {
    overflow: scroll;
}

.playlist > div {
    border: 1px solid black;
    text-align: center;
    padding: 2px;
}

.titleSong {
    text-align: center;
    margin: 10px;
}

.infoSong {
    display: grid;
    justify-items: center;
    grid-template-rows: 10% 70% 20%;
    gap: 10px;
}

.infoSong > img {
    max-width: 90%;
}